import React, { useState } from "react";
import styled from "styled-components";
import { Spinner } from "./form-sections";
import { media } from "./media-queries";
import { SubmitButton } from "../components/ButtonLink";
import { LabeledInput } from "../components/LabeledInput";
import { LabeledTextarea } from "../components/LabeledTextarea";
import { FormElementsColumnWrapper } from "../helpers/form-sections";
import InputValidIcon from "../images/input_valid.svg";
import { isEmailFormatValid } from "../helpers/email";

/*
        action="https://europe-west1-vosbor-io.cloudfunctions.net/function-vosbor-form-mailer"
        method="POST"
        autoComplete="off"
        enctype="multipart/form-data"
*/

export const FormType = {
  press: "press",
  info: "info",
};

export const ContactForm = ({ type }) => {
  const [name, setName] = useState("");
  const [company, setCompany] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [loading, setLoading] = useState(false);

  const isPressForm = type === FormType.press;

  const isFormSatisfied =
    name &&
    isEmailFormatValid(email) &&
    message &&
    (isPressForm ? !!company : true);

  const onFormSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitted(true);

    if (!isFormSatisfied) {
      return;
    }

    setLoading(true);

    const url =
      "https://europe-west1-vosbor-io.cloudfunctions.net/function-vosbor-form-mailer";
    const formData = new FormData();
    formData.append("type", type);
    formData.append("name", name);
    formData.append("company", company);
    formData.append("email", email);
    formData.append("message", message);
    const config = {
      method: "POST",
      mode: "cors",
      cache: "no-cache",
      body: formData,
    };

    try {
      const response = await fetch(url, config);

      console.log(response.data);
      window.location = "/response-ok";
    } catch (error) {
      console.log("ERROR:", error);
      setLoading(false);
    }
  };

  return (
    <FormWrapper>
      {loading && <Spinner />}
      <Form onSubmit={onFormSubmit}>
        <input type="hidden" name="type" value={type} />
        <FormElementsColumnWrapper>
          <LabeledInput
            type="text"
            placeholder="Enter full name"
            name="name"
            autoComplete="off"
            onChange={(e) => setName(e?.target?.value.trim())}
            disabled={loading}
            label="Full name"
            required
            icon={name && <InputValidIcon />}
            isInvalid={isSubmitted && name === ""}
          />
          <LabeledInput
            type="text"
            placeholder="Enter company name"
            name="company"
            autoComplete="off"
            onChange={(e) => setCompany(e?.target?.value.trim())}
            disabled={loading}
            label="Company"
            icon={company && <InputValidIcon />}
            required={isPressForm}
            isInvalid={isPressForm && isSubmitted && company === ""}
          />
        </FormElementsColumnWrapper>
        <LabeledInput
          type="email"
          placeholder="Enter email address"
          name="email"
          autoComplete="off"
          onChange={(e) => setEmail(e?.target?.value.trim())}
          disabled={loading}
          required
          label="Email address"
          icon={isEmailFormatValid(email) && <InputValidIcon />}
          isInvalid={isSubmitted && !isEmailFormatValid(email)}
        />
        <LabeledTextarea
          placeholder="Type message..."
          name="message"
          autoComplete="off"
          onChange={(e) => setMessage(e?.target?.value.trim())}
          disabled={loading}
          label="Message"
          required
          icon={message && <InputValidIcon />}
          isInvalid={isSubmitted && message === ""}
        />
        <InfoText>
          Your data will only be used by authorised Vosbor personnel, not
          distributed to third parties and only used to contact you and to send
          you our newsletter. By using the contact form you agree to our privacy
          policy.
        </InfoText>
        <StyledSubmitButton disabled={loading}>Submit</StyledSubmitButton>
      </Form>
    </FormWrapper>
  );
};

const FormWrapper = styled.div`
  ${media.m`
    display: flex;
    flex-direction: column;
    align-items: center;
  `}
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;
  align-items: stretch;

  ${media.m`
    max-width: 560px;
    width: 100%;
  `}
`;

const InfoText = styled.div`
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 9px;
  line-height: 18px;
  text-align: right;
  letter-spacing: 0.002em;
  color: #747487;
  ${media.m`
    max-width: 440px;
    align-self: end;
  `}
`;

const StyledSubmitButton = styled(SubmitButton)`
  margin-top: 24px;
  width: 100%;

  ${media.m`
    width: 160px;
  `}
`;
